@import "./../../assets/common-styles/common-mixins";

.profileWrapper {
  @include flexWrapper(column, flex-start, center, wrap, null);
  width: 100%;
  min-height: 100vh;

  .profileNavPanel {
    width: 80%;
    @include flexWrapper(column, center, center, wrap, null);
    @media screen and (max-width: 700px) {
      width: 100%;
      margin-bottom: 20px;
    }
  }
}